import {request} from "./request";

const ROOT_API = "api";

export const commentService = {
    getAuthors: () =>
        request({
            rootApi: ROOT_API,
            url: "authors",
            errorMessage: "   ",
        }),

    getCommentsPage: (page: number) =>
        request({
            rootApi: ROOT_API,
            url: "comments",
            errorMessage: `  ${page}  `,
            params: {page},
        }),
};
